import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http/index';
import { Table } from '../../../components';
import {
  getAllUserSaga,
  getActiveInActiveUserCountSaga,
  // editBlockUserStatusSaga,
} from '../../../store/actions';

// eslint-disable-next-line no-unused-vars
const Users = props => {
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const {
    users: { list: allData, totalRecords },
    userCount: { activeUserCount, inActiveUCount },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.user);

  const dispatch = useDispatch();

  const getData = (columnName, orderBy, search, skip, limit) => {
    if (search && skip !== 0) {
      dispatch(getAllUserSaga({ columnName, orderBy, search, skip: 0, limit }));
    } else {
      dispatch(getAllUserSaga({ columnName, orderBy, search, skip, limit }));
    }
  };

  useEffect(() => {
    setMounted(true);
    dispatch(getActiveInActiveUserCountSaga());
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  // const statusAction = data => {
  //   dispatch(editBlockUserStatusSaga({ id: data.id, isBlock: data.status }));
  // };

  const tableFields = [
    {
      displayName: 'Full Name',
      key: 'name',
    },
    {
      displayName: 'Email',
      key: 'email',
    },
    {
      displayName: 'Phone Number',
      key: 'phone',
    },
    // {
    //   displayName: 'Status',
    //   key: 'isBlock',
    //   noSort: true,
    //   isStatus: true,
    //   positiveValue: false,
    //   action: statusAction,
    // },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: '/vendors/users/user-detail',
        },
      ],
    },
  ];

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Users </h2>
              </div>
              <div className="colours_action">
                <ul className="animated zoomIn">
                  <li>
                    <div className="icons-circle-number dark-green">
                      {activeUserCount}
                    </div>
                    <h4>Active Users</h4>
                  </li>
                  <li>
                    <div className="icons-circle-number red">
                      {inActiveUCount}
                    </div>
                    <h4>Inactive Users</h4>
                  </li>
                  <li>
                    <div className="icons-circle-number violet">
                      {activeUserCount + inActiveUCount}
                    </div>
                    <h4>Total Users</h4>
                  </li>
                </ul>
              </div>
              <div className="coman-table" id="owner-detail-btn">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  linkPropsName="_id"
                  // link={linkObj}
                  // statusAction={statusAction}
                  setSearchFlag={searchFlagSetter}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SetTokenHeader(Users, axios);
